import jssdk from "../jssdk";

const doc = document;

const asyncParameters = {
  success: (res) => console.log("success", res),
  fail: (err) => console.error("error", err),
  complete: (res) => {
    if (res.resultCode === "000000") {
      console.log("complete", res);
    }
  },
};

doc.getElementById("button-navigate-back").onclick = () => {
  jssdk.navigateBack();
};

doc.getElementById("button-switch-tab").onclick = () => {
  const v = doc.getElementById("input-switch-tab").value;
  jssdk.switchTab({ index: v, ...asyncParameters });
};

doc.getElementById("button-show-navigation-bar-loading").onclick = () => {
  jssdk.showNavigationBarLoading({
    ...asyncParameters,
  });
};
doc.getElementById("button-hide-navigation-bar-loading").onclick = () => {
  jssdk.hideNavigationBarLoading({
    ...asyncParameters,
  });
};
doc.getElementById("button-set-navigation-bar-title").onclick = () => {
  const v = doc.getElementById("input-set-navigation-bar-title").value;
  jssdk.setNavigationBarTitle({
    title: v,
    ...asyncParameters,
  });
};
doc.getElementById("button-set-navigation-bar-color").onclick = () => {
  const theme = doc.getElementById("select-set-navigation-bar-color").value;
  const frontColor = doc.getElementById("input-set-navigation-bar-color").value;
  jssdk.setNavigationBarColor({
    theme,
    frontColor: `#${frontColor}`,
    ...asyncParameters,
  });
};
