  sendUdpMessage({
    message,
    host,
    port,
    success,
    fail,
    complete,
  } = {}) {
    this.createAsync({
      method: 'sendUdpMessage',
      success,
      fail,
      complete,
      parameters: {
        message,
        host,
        port,
      },
    });
  }

  onUdp(callback = () => {}) {
    // Android 平台，udp message 消息无法通过 JSON.parse 正常解析
    // 这里针对安卓进行 base64 转换处理
    if (!this.doctor()) {
      callback(CMIOT_NO_EXIST);
      return;
    }

    if (!this.canIUse('onUdp')) {
      fail(PLATFORM_NO_SUPPORT);
      return;
    }

    const _callback = `onUdp_callback_${getUuid()}`;

    const dealResult = (result) => {
      switch (this.platform) {
        case "Android":
          const _result = JSON.parse(result.replace(/\n/g, "\\n").replace(/\r/g, "\\r"));
          if (_result.data && _result.data.message) {
            _result.data.message = atob(_result.data.message);
          }
          return _result;
        case "IOS":
          return result;
        default:
          return null;
      }
    }

    window.CMIOT[_callback] = (result) => callback(dealResult(result));
    this.action({
      method: 'onUdp',
      callback: `CMIOT.${_callback}`,
    });
  }
  /********** 01 UDP End **********/
